* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	appearance: none;
}
$black: #000000;
$darkgrey: #303030;
$lightgrey: #999999;
$white: #ffffff;


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

@font-face {
	font-family: 'Icon';
	src: 
		local('fonts/icon'), url('../media/fonts/icon/icon.ttf'),
		local('fonts/icon'), url('../media/fonts/icon/icon.woff2');
	font-display: block;
}
* {
	color: $darkgrey;
	scroll-behavior: smooth;
	scrollbar-width: none;
}

*::-webkit-scrollbar {
    display: none;
    -webkit-appearance: none;
}
* {
	font-family: 'Inter', sans-serif;
	font-size: 18px;
	line-height: 1.5
}

@media (max-width: 950px) {

    .clock__time {

      & .time__hour, 
      & .time__points,
      & .time__minute {        
        font-size: 150px !important;
      }

	  & .time__timezone {
		margin-bottom: 8px !important;
	  }

	  
    }
	
	.panel .value {
	  font-size: 32px !important;
	}

}


@media (max-width: 750px) {

    .clock__time {

      & .time__hour, 
      & .time__points,
      & .time__minute {        
        font-size: 100px !important;
      }

	}

}

@media (max-width: 550px) {

    .clock__time {

      & .time__hour, 
      & .time__points,
      & .time__minute {        
        font-size: 60px !important;
      }
	    
	  & .time__timezone {
    	font-size: 24px !important;
		margin-bottom: 0 !important;
  	  }

	}

  	.clock__location, .location {
		margin-bottom: 32px !important;

		&__city,
		&__country {
			font-size: 18px !important;
		}

		

  }

}


.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'Icon';
  color: inherit;
  flex-shrink: 0;
  speak: none;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

$icons: (
  arrow: 'ea02',
  moon: 'ea03',
  sun: 'ea04',
  reload: 'ea05',
);

@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

@each $name, $code in $icons {
  .icon--#{"" +$name}::before {
    content: unicode($code);
  }
}
@keyframes timePointsPulse {
  0% { opacity: 1 }
  50% { opacity: 0 }
  100%{ opacity: 1 }
}

@keyframes backgroundSlide {
  0% { background-position: 50% }
  25% { background-position: 100% }
  75% { background-position: 0% }
  100% { background-position: 50% }
}
.clock {
  
  &__inner {
    display: grid;
    height: 100vh;
    min-height: 800px;
    grid-template-areas: 
      "quote ."
      "greeting . "
      "time toggle"
      "location toggle";
    grid-template-columns: 1fr 160px;
    grid-template-rows: 1fr auto auto auto;
    justify-content: start;
    align-items: start;
    padding: 56px 160px;
    transition: all 0.3s ease;
  }

  &__quote, .quote {
    grid-area: quote;
    max-width: 400px;
    margin-bottom: 40px;
    
    &__text {
      color: $white;
      margin-bottom: 12px;

      &::before { content: "\""; }
      &::after { content: "\""; }
    }

    &__author, .author {
      color: $white;
      font-weight: 700;

    }

  }

  &__greeting, .greeting {
    grid-area: greeting;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-bottom: 32px;
    
    &__icon {
      color: $white;
      line-height: 1.5;
      margin-right: 16px;
    }
    
    &__text, 
    &__status  {
      font-size: 20px;
      color: $white;
    }

  }

  &__time, .time {
    grid-area: time;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    margin-bottom: 32px;
    
    &__time, .time {
      
      &__hour, 
      &__points,
      &__minute {        
        color: $white;
        font-size: 200px;
        font-weight: 700;
        line-height: 1;
      }

      &__minute {
        margin-right: 32px;
      }

      &__points  {
        animation-duration: 1s;
        animation-name: timePointsPulse;
        animation-iteration-count: infinite;
        animation-timing-function: ease;
      }

    }

    &__timezone {
      color: $white;
      margin-bottom: 12px;
      font-size: 40px;
    }

  }

  &__location, .location {
    grid-area: location;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;


    &__city {
      color: $white;
      text-transform: uppercase;
      margin-right: 16px;
      font-size: 24px;
      font-weight: 700;
      letter-spacing: 4px;
      
      &::after {
        content: ",";
        color: $white;
      }
    
    }

    &__country {
      color: $white;
      text-transform: uppercase;
      font-size: 24px;
      font-weight: 700;  @media (max-height: 800px) {
        overflow-y: scroll;
       }
      letter-spacing: 4px;
    }

  }

  &__toggle, .toggle {
    background-color: $white;
    grid-area: toggle;
    width: 160px;
    height: 56px;
    border-radius: 56px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 8px 8px 8px 24px;
    cursor: pointer;
    align-self: end;

    &__input, .input {
      width: 40px;
      height: 40px;
      background-color: $lightgrey;
      border-radius: 50%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-content: center;
      transition: all 0.5s ease;

      &__icon {
        color: $white;
        font-size: 24px;
        align-self: center;
        transform: rotate(180deg);
        transition: all 0.5s ease;
      }

    }

    &__label {
      line-height: 1;
      font-size: 18px; 
      margin-top: 4px;
      font-weight: 700;
      letter-spacing: 4px;
      color: $black;
      opacity: 0.5;
      text-transform: uppercase;
    }

    &--inactive {
      
      & .input__icon {
        transform: rotate(0deg);
      }

      & .input {
        background-color: $darkgrey;
      }
      

    }

  }

  @media (max-width: 1250px) {

    &__inner {
      padding: 56px;
    }

  }

   @media (max-width: 1050px) {

   &__inner {
      grid-template-areas: 
        "quote"
        "greeting"
        "time"
        "location" 
        "toggle";
      grid-template-columns: auto;
      grid-template-rows: 1fr auto auto auto auto;
    }

    &__location {
      margin-bottom: 80px;
    }

  }

  @media (max-width: 950px) {

    &__greeting, .greeting {

      &__status {
        display: none;
      }

    }

    &__greeting, 
    &__time {
      margin-bottom: 16px !important;
    }

  }

  @media (max-width: 750px) {
 
    &__inner {
     min-height: 600px;
    }
    
    &__toggle, .toggle {
      width: 104px;
      height: 40px;
      border-radius: 40px;
      padding: 4px 4px 4px 12px;
      cursor: pointer;
      align-self: end;

      &__input, .input {
        width: 32px;
        height: 32px;

        &__icon {
          font-size: 16px;
        }

      }

      &__label {
        font-size: 12px; 
        margin-top: 2px;
      }

    }

  }

  @media (max-width: 550px) {
    
    &__greeting, 
    &__time {
      margin-bottom: 8px !important;
    }

    &__time, .time {

      &__minute {
        margin-right: 8px !important;
      }

    }

  }

  @media (max-width: 400px) {

    &__inner {
      padding: 56px 24px;
    }

  }

  @media (max-height: 800px) {
   overflow-y: scroll;
  }

}
.page {
	width: 100vw;
	min-height: 100vh;
	background-size: cover;
	background-position: 50%;
	position: relative;
	opacity: 0;
	transition: opacity 0.3s ease;
	overflow: hidden;
	animation-duration: 400s;
	animation-name: backgroundSlide;
	animation-iteration-count: infinite;
	animation-timing-function: ease;

	&__overlay {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: $white;
		pointer-events: none;
		opacity: 1;
		transition: opacity 0.3s ease;
		display: none;
	}

	&--day {
		background-image: url('/media/images/background-day.webp');
	}

	&--night {
		background-image: url('/media/images/background-night.webp');
	}

	&--day, 
	&--night {
		opacity: 1;

		& .page__overlay {
			opacity: 0;
		}

	}

  @media (max-height: 800px) {
   overflow-y: scroll;
  }

}
.panel {
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(4.7742px); 

  &__inner {
    padding: 56px 160px;
    display: grid;
    grid-template-areas: 
      "timezone day-of-the-week"
      "day-of-the-year week-number";
  }

  & .label {
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 2px;;
  }

  & .value {
    font-size: 56px;
    font-weight: 700;
  }

  &__timezone {
    grid-area: timezone;
  }

  &__day-of-week {
    grid-area: day-of-the-week;
  }

  &__day-of-year {
    grid-area: day-of-the-year;
  }

  &__week-number {
    grid-area: week-number;
  }

  &__timezone,
  &__day-of-week,
  &__day-of-year,
  &__week-number {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  
  &__day-of-week,
  &__week-number {
    border-left: 1px solid $darkgrey;
    padding-left: 64px;
  }

  &__timezone,
  &__day-of-week {
    padding-bottom: 32px;
  }

    @media (max-width: 1250px) {

    &__inner {
      padding: 56px;
    }

  };

  @media (max-width: 950px) {

  &__inner {
    grid-template-areas: 
      "timezone" 
      "day-of-the-year" 
      "day-of-the-week"
      "week-number";
  }

  &__day-of-week,
  &__week-number {
    border-left: none;
    padding-left: 0;

  }

  &__timezone,
  &__day-of-year,
  &__week-number,
  &__day-of-week {
    padding-bottom: 32px;
  }

  &__timezone,
  &__day-of-week,
  &__day-of-year,
  &__week-number {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  }

    @media (max-width: 750px) {

    &__timezone,
    &__day-of-week,
    &__day-of-year,
    &__week-number {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
  }

  }

  @media (max-width: 400px) {

    &__inner {
      padding: 56px 24px;
    }

  }


}